
import { createStore } from 'vuex'
import axios from 'axios'
import { reactive } from 'vue';

export const state = reactive({
    todos : []
})

export const mutations = {

    markCompleted: (state, id) => {
        state.todos.map( todo => todo.id == id ? todo.completed = !todo.completed : todo);
    },

    addTodo: (state, todo) => {
        console.log(todo);
        state.todos.push({ id: todo.id, title: todo.params.title, completed: false });
    },

    deleteTodo: (state, id) => {

        const todo = state.todos.find(todo => todo.id == id);

        if (todo != undefined)
            state.todos.pop(todo);


        console.log('DELTE TODO ID: %O', id);
        console.log('DELTE TODO: %O', todo);
        console.log('STATE TODOS: %O', state.todos);
    },

    deleteAll : (state) => {
        state.todos = [];
    },

    fetchTodos: (state, todos) => {
        state.todos = todos;
    }

}

export const actions = {

    increment({commit}) {
        commit('increment');
    },

    fetchTodos: async ({commit}) => {

        const response = await axios.get('https://jsonplaceholder.typicode.com/todos', {
            params: {
                _limit:5
            }
        });

        commit('fetchTodos', response.data);
        console.log('FETECH FIRED %O', response.data);
    },

    addTodo : async ({commit}, payload) => {
        console.log('Payload add TODO %O', payload);
        const response = await axios.post('https://jsonplaceholder.typicode.com/todos', {
            params: {
                title: payload.title
            }
        })

        commit('addTodo', response.data);
    },
    markCompleted: ({ commit }, payload) => {
        commit('markCompleted', payload.id);
    },
    deleteTodo: ({ commit }, payload) => {
        commit('deleteTodo', payload.id);
    },

    deleteAll: ({ commit }) => {
        commit('deleteAll');
    }

}

export const getters = {
    getTodos(state) {
        return state.todos;
    }
}

export default createStore({
    state,
    mutations,
    actions,
    getters
})
